:root {
  --bg: #363333;
  --pFont: #e16428;
  --cardBg: #272121;
  --sFont: #f6e9e9;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: var(--bg);
  color: var(--sFont);
  height: 100vh;
  display: grid;
  place-content: center;
  padding: 1rem;
}
